import React from "react";
import { connect } from "react-redux";
import { firestoreConnect } from "react-redux-firebase";
import { compose } from "redux";
import { Redirect } from "react-router-dom";
import moment from "moment";

const ProjectDetails = (props) => {
	const { project, auth } = props;
	if (!auth.uid) return <Redirect to="/signin" />;
	if (project) {
		return (
			<div className="container section project-details">
				<div className="card z-depth-0">
					<div className="card-content">
						<span className="card-title purple-text">{project.title}</span>
						<p>{project.details}.</p>
					</div>
					{/* <div class="card-action">
						<button className=" btn grey-text text-darken-3">
							<i className="material-icons purple-text">edit</i>Edit
						</button>
						<button className="btn grey-text text-darken-3">
							<i className="material-icons purple-text">delete</i>Delete
						</button>
					</div> */}
					<div className="card-action grey lighten-4 purple-text">
						<div>
							Posted by {project.authorFirstName} {project.authorSecondName}
						</div>
						<div> {moment(project.createdAt.toDate()).calendar()}</div>
					</div>
				</div>
			</div>
		);
	} else {
		return (
			<div className="container center">
				<p>Loading project...</p>
			</div>
		);
	}
};

const mapStateToProps = (state, ownProps) => {
	const id = ownProps.match.params.id;
	const projects = state.firestore.data.projects;
	const project = projects ? projects[id] : null;
	return {
		project: project,
		auth: state.firebase.auth,
	};
};

export default compose(
	firestoreConnect([{ collection: "projects" }]), // or { collection: 'todos' }
	connect(mapStateToProps)
)(ProjectDetails);
